import React from 'react';
import './List.scss'
import ListItem from '../list-item/ListItem'

function List({list, setList}) {

    function remove(id) {
        let filteredList = list.filter((listItem) => listItem.id !== id)
        setList(filteredList)
    }

    function edit(id) {
        let filteredItem = list.filter((listItem) => listItem.id === id)
        let newValue = prompt('Change me')
        if (newValue !== "") {
            filteredItem[0].value = newValue
            setList([...list])
        }
        console.log(filteredItem)
    }

    return (
        <div className="scroll-container">
            <ul>{list.map(({value, id}) => <ListItem
                    key={id}
                    id={id}
                    value={value}
                    editItem={() => edit(id)}
                    removeFromList={() => remove(id)}/>)}</ul>
        </div>
    )
}

export default List